
.task-operation .operation-table {
  width: 100%;
  font-size: 14px;
  border: 1px solid #ebebeb;
  border-collapse: collapse;
}
.task-operation .operation-table th {
    background: #fafafa;
}
.task-operation .operation-table th, .task-operation .operation-table td {
    padding: 10px;
    color: #666666;
    text-align: left;
    border: 1px solid #ebeef5;
}
